import React from 'react';
import { replace } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { ROUTES } from 'store/constants/router';
import { closeAppAction } from 'store/actions/app';
import { Pin, SadFaceIcon } from 'components/Pin/Pin';
import { ButtonViews } from 'components/Button/Button';
import {
    LocationButton,
    LocationMapBackground,
    LocationPage,
    LocationText,
    LocationTitle,
} from 'components/LocationPage/LocationPage';

export const NoArea: React.FC = () => {
    const dispatch = useDispatch();
    const address = useSelector(({ userModel }) => userModel.address);
    const city = useSelector(({ userModel }) => userModel.city);

    const handleChangeButtonClick = React.useCallback(() => {
        dispatch(replace(ROUTES.ADDRESS_SIMPLE_GUIDE));
    }, [dispatch]);

    const handleCloseButtonClick = React.useCallback(() => {
        dispatch(closeAppAction());
    }, [dispatch]);

    const addressString = [city, address].join(', ');

    const buttons = React.useMemo(
        () => (
            <>
                <LocationButton view={ButtonViews.primary} shouldFocusOnMount onClick={handleChangeButtonClick}>
                     
                </LocationButton>
                <LocationButton onClick={handleCloseButtonClick} view={ButtonViews.secondary}>
                     
                </LocationButton>
            </>
        ),
        [handleChangeButtonClick, handleCloseButtonClick],
    );

    const background = React.useMemo(() => <LocationMapBackground />, []);

    return (
        <LocationPage buttons={buttons} background={background}>
            <Pin pinColor="bad">
                <SadFaceIcon />
            </Pin>
            <LocationTitle>{addressString}</LocationTitle>
            <LocationText> ,      </LocationText>
        </LocationPage>
    );
};
