import React from 'react';
import styled from 'styled-components';
import { Display3, Headline3 } from '@sberdevices/plasma-styles';
import { primary } from '@sberdevices/plasma-tokens';
import { useSelector } from 'hooks/useSelector';
import { formatPhone } from 'tools/phoneNumber';
import { DialogPage } from 'components/DialogPage/DialogPage';
import { MultilineText } from 'components/MultilineText/MultilineText';

const StyledText = styled(Headline3)`
    margin-bottom: 48px;
    color: ${primary};
`;

const StyledPhone = styled(Display3)`
    color: ${primary};
`;

export const StatusReport: React.FC = () => {
    const reportPhone = useSelector((state) => state.orderStatusData.reportPhone);

    const phoneNumber = React.useMemo(() => formatPhone(reportPhone), [reportPhone]);

    return (
        <DialogPage>
            <StyledText>
                <MultilineText text={' -   ,\n    '} />
            </StyledText>
            <StyledPhone>{phoneNumber}</StyledPhone>
        </DialogPage>
    );
};
