import { isMobile, isPortal } from 'tools/environment';

import { RootDesktop } from './Root@desktop';
import { RootPortal } from './Root@portal';
import { RootTouch } from './Root@touch';

function getRoot() {
    if (isMobile) {
        return RootTouch;
    }

    if (isPortal) {
        return RootPortal;
    }

    return RootDesktop;
}

export const Root = getRoot();
