import styled from 'styled-components';
import { Body1, Footnote1, Headline1 } from '@sberdevices/plasma-styles';
import { primary, secondary, surfaceLiquid01 } from '@sberdevices/plasma-tokens';
import { GradientFade } from 'components/GradientFade/GradientFade';
import { PlasmaIcon } from 'components/PlasmaIcon/PlasmaIcon';
import { useFocusOnMount } from 'hooks/useFocusOnMount';
import React from 'react';

const circleContainerSize = 316;
const plasmaIconSize = 48;
const offsetToCenteringIcon = circleContainerSize / 2 - plasmaIconSize / 2;
const StyledPlasmaIcon = styled(PlasmaIcon)`
    position: absolute;
    height: ${plasmaIconSize}px;
    width: ${plasmaIconSize}px;
    top: ${offsetToCenteringIcon}px;
    left: ${offsetToCenteringIcon}px;
`;

export const StyledCircles = styled.div`
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: ${circleContainerSize}px;
    width: ${circleContainerSize}px;
    margin-top: 106px;

    &::after {
        position: absolute;
        top: 86px;
        left: 86px;
        right: 86px;
        bottom: 86px;

        content: '';
        border-radius: 50%;

        opacity: 0.3;
        background: ${surfaceLiquid01};
        box-shadow: inset 0 4px 4px rgba(255, 255, 255, 0.15);
        backdrop-filter: blur(48px);
    }

    &::before {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;

        content: '';
        border-radius: 50%;

        opacity: 0.3;
        background: rgba(255, 255, 255, 0.02);
        box-shadow: inset 0 4px 4px rgba(255, 255, 255, 0.15);
        backdrop-filter: blur(48px);
    }
`;

const StyledOkButton = styled(Footnote1)`
    position: relative;
    display: inline-flex;
    height: 78px;
    width: 78px;
    justify-content: center;
    align-items: center;
    color: ${primary};
    margin-left: 12px;

    &::before {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;

        content: '';
        border-radius: 50%;
        z-index: -1;

        opacity: 0.3;
        background: ${surfaceLiquid01};
        box-shadow: inset 0 4px 4px rgba(255, 255, 255, 0.15);
        backdrop-filter: blur(48px);
    }
`;

export const GuidePageRoot = styled.div`
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
`;

export const GuidePagePlasmaIcon = () => (
    <GradientFade sizePx={50} isVisible kind="bottom">
        <StyledCircles>
            <StyledPlasmaIcon />
        </StyledCircles>
    </GradientFade>
);

export const GuidePageTitle = styled(Body1)`
    margin-top: -54px;
    color: ${primary};
`;

export const GuidePageSource = styled(Headline1)`
    position: relative;
    margin-top: 32px;

    background: linear-gradient(to right, #00ffdf, #00ff5a);
    color: transparent;
    -webkit-background-clip: text;
`;

export const GuidePageSubtitle = styled(Body1)`
    margin-top: 130px;
    color: ${secondary};
`;

interface GuidePageHintProps {
    onOkClick: () => void;
}
export const GuidePageHint: React.FC<GuidePageHintProps> = ({ onOkClick }) => {
    const okButtonRef = useFocusOnMount<HTMLDivElement>();

    return (
        <GuidePageSubtitle>
               ,    
            <StyledOkButton ref={okButtonRef} tabIndex={0} onClick={onOkClick}>
                
            </StyledOkButton>
        </GuidePageSubtitle>
    );
};
