import React from 'react';
import styled from 'styled-components';
import { background, gradient } from '@sberdevices/plasma-tokens';
import { Header } from 'components/Header/Header';
import { Theme } from 'components/Theme/Theme';
import { TransparentPixel } from 'components/TransparentPixel/TransparentPixel';
import { layout } from 'style/const/layout';

const StyledContainer = styled.div`
    position: relative;

    width: 100%;
    height: 100%;

    overflow: hidden;

    background-color: ${background};
    background-image: ${gradient};
`;

const AppContent = styled.div`
    position: relative;
    z-index: 2;

    height: ${layout.contentHeightPx}px;
`;

const StyledHeader = styled(Header)`
    z-index: 3;
`;

const preventScroll = (event: React.SyntheticEvent<HTMLDivElement>) => {
    event.currentTarget.scrollTop = 0;
};

export const AppLayout: React.FC = ({ children }) => {
    return (
        <StyledContainer onScroll={preventScroll}>
            <Theme />
            <StyledHeader />
            <TransparentPixel />
            <AppContent>{children}</AppContent>
        </StyledContainer>
    );
};
