import React from 'react';
import styled from 'styled-components';
import { Footnote1, Headline4, Caption } from '@sberdevices/plasma-styles';
import { primary, secondary } from '@sberdevices/plasma-tokens';
import { useSelector } from 'hooks/useSelector';
import { useContainerFocus } from 'hooks/useContainerFocus';
import { selectHeader } from 'store/selectors';
import { icon } from 'style/const/icon';
import { layout } from 'style/const/layout';
import { transition } from 'style/const/transition';

import { HeaderAddress } from '../HeaderAddress/HeaderAddress';
import { HeaderCart } from '../HeaderCart/HeaderCart';

import { ReactComponent as ChevronLeftSvg } from './Header.assets/chevron-left.svg';
import { ReactComponent as LogoSvg } from './Header.assets/logo.svg';

const headerPaddingTopPx = 32;

interface StyledHeaderProps {
    isVisible: boolean;
}

const StyledHeader = styled.div<StyledHeaderProps>`
    --spatial-navigation-contain: contain;

    position: relative;

    height: ${layout.headerHeightPx}px;

    box-sizing: border-box;
    padding: ${headerPaddingTopPx}px 88px 0 128px;

    display: flex;
    justify-content: space-between;
    align-items: center;

    opacity: ${({ isVisible }) => Number(isVisible)};

    transition: opacity ${transition.default};
`;

const backButtonTopPx = 24 + headerPaddingTopPx;

const backButtonIconSizePx = icon.m.sizePx;

const StyledBackButton = styled.span`
    position: absolute;
    top: ${backButtonTopPx}px;
    left: 24px;

    width: 80px;
    height: 80px;

    display: flex;
    align-items: center;
    justify-content: center;
`;

const StyledChevronLeft = styled(ChevronLeftSvg)`
    width: ${backButtonIconSizePx}px;
    height: ${backButtonIconSizePx}px;
`;

const StyledSection = styled.div`
    display: flex;
    align-items: center;
`;

const logoSizePx = 72;

const StyledLogo = styled(LogoSvg)`
    width: ${logoSizePx}px;
    height: ${logoSizePx}px;
    border-radius: 16px;
`;

const StyledTitle = styled(Headline4)`
    color: ${primary};
`;

const StyledSubtitle = styled(Footnote1)`
    color: ${secondary};
`;

const StyledCaption = styled(Caption)`
    margin-left: 24px;

    display: flex;

    flex-direction: column;
`;

const cartButtonRef = React.createRef<HTMLButtonElement>();

// eslint-disable-next-line @typescript-eslint/no-unused-vars
export const Header: React.FC = ({ children, ...props }) => {
    const { title, subtitle, isBackButtonVisible, isVisible: isHeaderVisible } = useSelector(selectHeader);
    const isAddressVisible = useSelector((state) => selectHeader(state).address.isVisible);
    const isCartVisible = useSelector((state) => selectHeader(state).cart.isVisible);
    const hasSubtitle = Boolean(subtitle);

    const handleReceiveFocus = React.useCallback(() => {
        if (isCartVisible) {
            cartButtonRef.current?.focus();
        }
    }, [isCartVisible]);

    const { handleFocus, handleBlur } = useContainerFocus({
        onReceiveFocus: handleReceiveFocus,
    });

    return (
        <StyledHeader isVisible={isHeaderVisible} {...props} onFocus={handleFocus} onBlur={handleBlur}>
            {isBackButtonVisible && (
                <StyledBackButton>
                    <StyledChevronLeft />
                </StyledBackButton>
            )}
            <StyledSection>
                <StyledLogo />
                <StyledCaption>
                    <StyledTitle>{title}</StyledTitle>
                    {hasSubtitle ? <StyledSubtitle>{subtitle}</StyledSubtitle> : null}
                </StyledCaption>
            </StyledSection>
            <StyledSection>
                <HeaderAddress isVisible={isAddressVisible} />
                <HeaderCart forwardRef={cartButtonRef} isVisible={isCartVisible} />
            </StyledSection>
        </StyledHeader>
    );
};
