import React from 'react';
import { goBack } from 'connected-react-router';
import { buildAddressString } from 'pages/AddressSimple/AddressSimple.tools';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { useAddressSimpleUpdateSuggests } from 'pages/AddressSimple/AddressSimple.hooks';
import { TwoGisSuggestFetchingStatus } from 'store/reducers/data/twoGisSuggestsData';
import { addressInputDeclineThunk, addressSubmitThunk } from 'store/thunks/native';
import { ButtonViews } from 'components/Button/Button';
import {
    LocationButton,
    LocationMapBackground,
    LocationPage,
    LocationText,
    LocationTitle,
} from 'components/LocationPage/LocationPage';
import { Pin, DeadFaceIcon, HappyFaceIcon } from 'components/Pin/Pin';

const TryAgain: React.FC = () => {
    const dispatch = useDispatch();
    const inputValue = useSelector(({ addressSimplePage }) => addressSimplePage.inputValue);

    const handleRepeatClick = React.useCallback(() => {
        dispatch(goBack());
    }, [dispatch]);

    const buttons = React.useMemo(
        () => (
            <LocationButton shouldFocusOnMount view={ButtonViews.secondary} onClick={handleRepeatClick}>
                  
            </LocationButton>
        ),
        [handleRepeatClick],
    );

    const background = React.useMemo(() => <LocationMapBackground />, []);

    return (
        <LocationPage buttons={buttons} background={background}>
            <Pin isBroken pinColor="bad">
                <DeadFaceIcon />
            </Pin>
            <LocationTitle>{inputValue}</LocationTitle>
            <LocationText>   ,   </LocationText>
        </LocationPage>
    );
};

const AddressFound: React.FC = () => {
    const dispatch = useDispatch();
    const isAddressSubmitting = useSelector(({ addressSimplePage }) => addressSimplePage.isAddressSubmitting);
    const suggest = useSelector(({ twoGisSuggestsData: { mostRepresentationSuggest } }) => mostRepresentationSuggest);

    const handleAcceptButtonClick = React.useCallback(() => {
        dispatch(addressSubmitThunk());
    }, [dispatch]);

    const handleDeclineButtonClick = React.useCallback(() => {
        dispatch(addressInputDeclineThunk());
    }, [dispatch]);

    const buttons = React.useMemo(
        () => (
            <>
                <LocationButton
                    shouldFocusOnMount
                    shouldLockAfterClick
                    view={ButtonViews.primary}
                    onClick={handleAcceptButtonClick}
                >
                    ,  
                </LocationButton>
                <LocationButton
                    view={ButtonViews.secondary}
                    onClick={handleDeclineButtonClick}
                    disabled={isAddressSubmitting}
                >
                    ,  
                </LocationButton>
            </>
        ),
        [handleAcceptButtonClick, handleDeclineButtonClick, isAddressSubmitting],
    );

    const addressName = React.useMemo(() => suggest && buildAddressString(suggest), [suggest]);

    return (
        <LocationPage buttons={buttons}>
            <Pin pinColor="good">
                <HappyFaceIcon />
            </Pin>
            <LocationTitle>{addressName}</LocationTitle>
            <LocationText>  ?</LocationText>
        </LocationPage>
    );
};

export const AddressSimpleQuestion: React.FC = () => {
    const suggest = useSelector(({ twoGisSuggestsData: { mostRepresentationSuggest } }) => mostRepresentationSuggest);
    const suggestsFetchingStatus = useSelector(({ twoGisSuggestsData }) => twoGisSuggestsData.fetchingStatus);

    useAddressSimpleUpdateSuggests();

    //    ,   ,    
    if (suggestsFetchingStatus === TwoGisSuggestFetchingStatus.PENDING) {
        return <LocationPage />;
    }

    if (!suggest) {
        return <TryAgain />;
    }

    return <AddressFound />;
};
