import React from 'react';
import { KeyboardExpandHandler, useKeyboardExpanded } from 'hooks/useKeyboardExpanded';

interface UseKeyboardCloseParams {
    handleInputSubmit?: () => void;
    handleKeyboardClose?: () => void;
}
export const useKeyboardClose = ({ handleInputSubmit, handleKeyboardClose }: UseKeyboardCloseParams) => {
    /**
     *     ?
     *   ,     ""    
     *  onSubmit    - .     
     *  ,        , 
     * useKeyboardExpanded       
     *  ,     . -   ()
     *      ,   .
     *       (     ""),
     *     ,      .  , .
     */
    const [isHandlerInvoked, setHandlerInvoked] = React.useState(false);

    /**
     *            
     * ,  ,  ,    ,  
     *   .
     */
    const onInputFocus = React.useCallback(() => {
        setHandlerInvoked(false);
    }, []);

    const onInputSubmit = React.useCallback(() => {
        if (isHandlerInvoked || !handleInputSubmit) {
            return;
        }

        setHandlerInvoked(true);

        handleInputSubmit();
    }, [handleInputSubmit, isHandlerInvoked, setHandlerInvoked]);

    const handleKeyboardExpand = React.useCallback<KeyboardExpandHandler>(
        (isKeyboardExpanded, keyboardExpandedCount) => {
            if (keyboardExpandedCount === 0 || isKeyboardExpanded) {
                return;
            }

            if (isHandlerInvoked || !handleKeyboardClose) {
                return;
            }

            handleKeyboardClose();
        },
        [isHandlerInvoked, handleKeyboardClose],
    );

    useKeyboardExpanded(handleKeyboardExpand);

    return { onInputFocus, onInputSubmit };
};
