import React from 'react';
import styled, { createGlobalStyle } from 'styled-components';
import { Provider as ReduxProvider } from 'react-redux';
import { Switch, Route } from 'react-router';
import { ConnectedRouter } from 'connected-react-router';
import { history } from 'tools/history';
import { store } from 'store';
import { ROUTES } from 'store/constants/router';
import { AppLayoutTouch } from 'components/AppLayout/AppLayout@touch';
import { CartTouch } from 'pages/Cart/Cart@touch';
import { AddressFullAllTouch } from 'pages/AddressFullAll/AddressFullAll@touch';
import { CatalogTouch } from 'pages/Catalog/Catalog@touch';
import { PromoTouch } from 'pages/Promo/Promo@touch';
import { SuccessTouch } from 'pages/Success/Success@touch';
import { AddressMapTouch } from 'pages/AddressMap/AddressMap@touch';

const GlobalStyle = createGlobalStyle`
    body { /* stylelint-disable-line selector-nested-pattern */
        margin: 0;
    }

    * { /* stylelint-disable-line selector-nested-pattern, selector-max-universal */
        outline: none;
    }
`;

const StyledContainer = styled.div`
    overflow: hidden;
    width: 100%;
    height: 100vh;
    outline: none;
    font-family: 'SB Sans Text', Helvetica, Arial, sans-serif;
    background: #000;
`;

const preventScroll = (event: React.SyntheticEvent<HTMLDivElement>) => {
    event.currentTarget.scrollTop = 0;
};

export const RootTouch: React.FC = () => (
    <ReduxProvider store={store}>
        <ConnectedRouter history={history}>
            <GlobalStyle />
            <StyledContainer onScroll={preventScroll}>
                <AppLayoutTouch>
                    <Switch>
                        <Route path={ROUTES.CATALOG}>
                            <CatalogTouch />
                        </Route>
                        <Route path={ROUTES.CART}>
                            <CartTouch />
                        </Route>
                        <Route path={ROUTES.ADDRESS_FULL_ALL}>
                            <AddressFullAllTouch />
                        </Route>
                        <Route path={ROUTES.SUCCESS}>
                            <SuccessTouch />
                        </Route>
                        <Route path={ROUTES.ADDRESS_SIMPLE}>
                            <AddressMapTouch />
                        </Route>
                        <Route path={ROUTES.PROMO}>
                            <PromoTouch />
                        </Route>
                    </Switch>
                </AppLayoutTouch>
            </StyledContainer>
        </ConnectedRouter>
    </ReduxProvider>
);
